export const get = (url, params = {}) => {
    return new Promise((resolve, reject) => {
        axios.get(url, {
            params: params
        })
            .then(response => {
                resolve(response.data);

            })
            .catch(err => {
                reject(err);
            })
    })
}

export const getConfig = (url, params = {}, config = {}) => {
    return new Promise((resolve, reject) => {
        console.log(config)
        axios.get(url, {
            headers: config
        })
            .then(response => {
                resolve(response.data);
            })
            .catch(err => {
                reject(err);
            })
    })
}

export const postConfig = (url, data = {}, config) => {
    return new Promise((resolve, reject) => {
        axios.post(url, data, config)
            .then(response => {
                resolve(response.data)
            })
            .catch(err => {
                reject(err);
            })
    })
}



export const post = (url, data = {}) => {
    return new Promise((resolve, reject) => {
        axios.post(url, data, { withCredentials: true })
            .then(response => {
                resolve(response.data)
            })
            .catch(err => {
                reject(err);
            })
    })
}
